package com.rdapps.weddinginvitation.ui

import androidx.compose.foundation.Image
import androidx.compose.foundation.layout.*
import androidx.compose.foundation.rememberScrollState
import androidx.compose.foundation.verticalScroll
import androidx.compose.material3.Button
import androidx.compose.material3.Icon
import androidx.compose.material3.MaterialTheme
import androidx.compose.material3.Text
import androidx.compose.runtime.*
import androidx.compose.ui.Alignment
import androidx.compose.ui.Modifier
import androidx.compose.ui.graphics.Color
import androidx.compose.ui.graphics.ColorFilter
import androidx.compose.ui.text.font.FontFamily
import androidx.compose.ui.text.style.TextAlign
import androidx.compose.ui.unit.coerceAtMost
import androidx.compose.ui.unit.dp
import com.rdapps.weddinginvitation.model.HashData
import com.rdapps.weddinginvitation.openUrl
import com.rdapps.weddinginvitation.util.*
import io.github.jan.supabase.SupabaseClient
import kotlinx.coroutines.delay
import kotlinx.coroutines.launch
import kotlinx.datetime.LocalDate
import kotlinx.datetime.LocalDateTime
import kotlinx.datetime.LocalTime
import kotlinx.datetime.Month
import org.jetbrains.compose.resources.Font
import org.jetbrains.compose.resources.painterResource
import org.jetbrains.compose.resources.stringResource
import org.jetbrains.compose.resources.vectorResource
import wedding_invitation.composeapp.generated.resources.*

@Composable
fun HomePage(userId: String, decodedHashData: HashData, supabase: SupabaseClient) {
    val coroutineScope = rememberCoroutineScope()

    BoxWithConstraints {
        val totalWidth = maxWidth

        Column(
            modifier = Modifier
                .fillMaxSize()
                .verticalScroll(rememberScrollState())
                .padding(horizontal = 20.dp, vertical = 40.dp),
            horizontalAlignment = Alignment.CenterHorizontally,
            verticalArrangement = Arrangement.Center
        ) {

            Image(
                painter = painterResource(Res.drawable.ganesh),
                contentDescription = "ganesha",
                modifier = Modifier
                    .padding(20.dp)
                    .width((totalWidth * 30 / 100).coerceAtMost(150.dp)),
                colorFilter = ColorFilter.tint(MaterialTheme.colorScheme.onPrimary)
            )

            VerticalSpacer(20)

            if (decodedHashData.name.isNotBlank()) {
                Text(
                    text = decodedHashData.name,
                    fontFamily = FontFamily(Font(Res.font.Mogra_Regular)),
                    style = MaterialTheme.typography.displaySmall,
                    textAlign = TextAlign.Center
                )
            }

            val welcomeText = if (decodedHashData.name.isBlank())
                stringResource(Res.string.welcome_text_without_name)
            else
                stringResource(Res.string.welcome_text)

            Text(
                text = welcomeText,
                style = MaterialTheme.typography.titleLarge,
                textAlign = TextAlign.Center
            )

            VerticalSpacer(20)

            Text(
                text = "Rajdeep & Reema",
                style = MaterialTheme.typography.displayLarge,
                textAlign = TextAlign.Center
            )

            VerticalSpacer(20)

            Text(
                text = "On 30th June, 2024 which is",
                style = MaterialTheme.typography.titleLarge
            )

            VerticalSpacer(10)

            val mrgDate = remember {
                LocalDateTime(
                    LocalDate(2024, Month.JUNE, 30),
                    LocalTime(9, 0, 0)
                )
            }
            var currentTime by remember {
                mutableStateOf(LocalDateTime.now())
            }

            val diff = mrgDate.toMillis() - currentTime.toMillis()
            if (diff > 0) {
                LaunchedEffect(Unit) {
                    launch {
                        while (true) {
                            delay(1000)
                            currentTime = LocalDateTime.now()

                            if (currentTime.compareTo(mrgDate) == 0) {
                                break
                                // TODO: write logic to show the message of congratulations
                            }
                        }
                    }
                }

                val circleWidth = ((totalWidth - 70.dp) / 4).coerceAtMost(120.dp)

                Row(
                    modifier = Modifier,
                    horizontalArrangement = Arrangement.spacedBy(10.dp)
                ) {

                    val seconds = diff / 1000
                    val minutes = seconds / 60
                    val hours = minutes / 60
                    val days = hours / 24

                    val remainingSeconds = seconds % 60
                    val remainingMinutes = minutes % 60
                    val remainingHours = hours % 24

                    CountdownItem(
                        days,
                        "Days",
                        modifier = Modifier.size(circleWidth)
                    )

                    CountdownItem(
                        remainingHours,
                        "Hours",
                        modifier = Modifier.size(circleWidth)
                    )

                    CountdownItem(
                        remainingMinutes,
                        "Minutes",
                        modifier = Modifier.size(circleWidth)
                    )

                    CountdownItem(
                        remainingSeconds,
                        "Seconds",
                        modifier = Modifier.size(circleWidth)
                    )

                }
            }

            VerticalSpacer(20)

            val calendarEventUrl = stringResource(Res.string.calendar_event_url)
            Button(
                onClick = {
                    openUrl(calendarEventUrl)
                    coroutineScope.launch {
                        supabase.sendEvent("Add to Calendar Clicked", userId)
                    }
                }
            ) {
                Icon(
                    imageVector = vectorResource(Res.drawable.round_calendar_month_24),
                    contentDescription = null
                )
                HorizontalSpacer(10)
                Text("Add to Calendar")
            }

            VerticalSpacer(20)

            val venueUrl = stringResource(Res.string.venue_url)
            Button(
                onClick = {
                    openUrl(venueUrl)
                    coroutineScope.launch {
                        supabase.sendEvent("Venue Clicked", userId)
                    }
                }
            ) {
                Icon(
                    imageVector = vectorResource(Res.drawable.round_location_on_24),
                    contentDescription = null
                )
                HorizontalSpacer(10)
                Text("Venue")
            }

            if (decodedHashData.showStayDetails) {
                VerticalSpacer(20)

                val stayUrl = stringResource(Res.string.stay_url)
                Button(
                    onClick = {
                        openUrl(stayUrl)
                        coroutineScope.launch {
                            supabase.sendEvent("Stay Clicked", userId)
                        }
                    }
                ) {
                    Icon(
                        vectorResource(Res.drawable.round_location_on_24),
                        contentDescription = null
                    )
                    HorizontalSpacer(10)
                    Text("Stay")
                }
            }

            if (decodedHashData.showContactNumber) {
                VerticalSpacer(20)

                Button(
                    onClick = {
                        openUrl("tel:+919033159066")
                        coroutineScope.launch {
                            supabase.sendEvent("Call Clicked", userId)
                        }
                    }
                ) {
                    Icon(
                        vectorResource(Res.drawable.round_call_24),
                        contentDescription = null
                    )
                    HorizontalSpacer(10)
                    Text("Call")
                }
            }

            VerticalSpacer(20)

            Button(
                onClick = {
                    openUrl("../License.pdf")
                }
            ) {
                Text("Gujarati Kankotri")
            }
        }
    }
}